#myarse {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh; }
#myarse * {
    display: inline;
    opacity: 1;
    fill-opacity: 1; }
#myarse .background {
    fill: #332272; }
#myarse .water .h2o {
    fill: #005e89; }
#myarse .water .light {
    fill: #ffffff;
    opacity: 0.6;}
#myarse .water .shallow {
    fill: #ffffff;
    opacity: 0.1; }
#myarse .ship{
    fill: #2eafc0; }
#myarse .ship .wave {
    fill: #222;
    opacity: 0.4; }
#myarse .mount.lvl0 {
    fill: #000000;
    opacity: 0.6; }
#myarse .mount.lvl1 {
    fill: #000000; }
#myarse .mount.lvl2 .body, #myarse .mount.lvl3 .body {
    fill: #0bb79a; }
#myarse .mount.lvl2 .snow, #myarse .mount.lvl3 .snow {
    fill: #ffffff; }
#myarse .mount.lvl2 .shadow, #myarse .mount.lvl3 .shadow {
    fill: #000000;
    opacity: 0.4; }
#myarse .land .far {
    fill: #9aca3a; }
#myarse .land .close {
    fill: #8fbc38; }



